

import React from 'react';
import RaisedButton from 'material-ui/RaisedButton';
import Popover from 'material-ui/Popover';
import Menu from 'material-ui/Menu';
import MenuItem from 'material-ui/MenuItem';
import injectTapEventPlugin from 'react-tap-event-plugin';
import IconButton from 'material-ui/IconButton';
import ImageCamera from 'material-ui/svg-icons/image/camera';
import ImagePhotoLibrary from 'material-ui/svg-icons/image/photo-library';
import FloatingActionButton from 'material-ui/FloatingActionButton';
import ContentAdd from 'material-ui/svg-icons/content/add';
const style = {
  marginRight: 20,
};
//injectTapEventPlugin();

 class PopCamera extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      open: false,
      postDialogOpen :false,
    };

    this.handleTouchTap = this.handleTouchTap.bind(this);
    this.handleRequestClose = this.handleRequestClose.bind(this);
  }

  handleTouchTap(event){
    // This prevents ghost click.
    event.preventDefault();

    this.setState({
      open: true,
      anchorEl: event.currentTarget,
    });
  };

  handleRequestClose(){
    this.setState({
      open: false,
    });
  };

  render() {

     const actions = [
      <FlatButton
        label="Post"
        primary={true}
        onTouchTap={this.handleCloseAndPost}

      />,
      
    ];
    return (
      <div>
    
     <FloatingActionButton onTouchTap={this.handleTouchTap} mini={true} secondary={true} style={style}>
      <ContentAdd />
    </FloatingActionButton>
       <Dialog
          title="Create a post"
          actions={actions}
          modal={false}
          open={this.state.postDialogOpen}
          onRequestClose={this.handlePostDialogClose}
        >
           <div>
    <TextField
      hintText="Enter photo url"
      onChange = {this.handleChange}
      value={this.state.uname}
      floatingLabelText="Username"
    /><br />
    <br />
    <TextField
      hintText="Enter hashtag"
      floatingLabelText="Password"
      type="password"
    /><br /></div>
        </Dialog>
        
      </div>
    );
  }
}

export default PopCamera;

